/* GENERAL */

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

*{
  margin: 0;
  padding: 0;
}

html{
  scroll-behavior: smooth;
}

body{
  font-family: "Poppins", sans-serif;
  background-color: rgb(245, 245, 245);
}

h2, h3{
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* DESKTOP NAV */

nav, .nav-links{
  display: flex;
}

nav{
  justify-content: space-around;
  align-items: center;
  height: 17vh;
}

.nav-links{
  gap: 2em;
  list-style: none;
  font-size: 1.5em;
}

.text-container{
  margin-top: 1em;
}

.fine-text{
  color: rgb(55, 55, 55);
  text-align: center;
}

#key-bold{
  color: rgb(0, 125, 251);
}

a{
  color: black;
  text-decoration: none;
  text-decoration-color: white;
}

a:hover{
  color: grey;
  text-decoration: underline;
  text-underline-offset: 1em;
  text-decoration-color: rgb(0, 109, 251);
}

.logo{
  font-size: 2em;
}

.logo:hover{
  cursor: default;
}

/* HAMBURGER MENU */

#hamburger-nav{
  display: none;
}

.hamburger-menu{
  position: relative;
  display: inline-block;
}

.hamburger-icon{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 24px;
  width: 30px;
  cursor: pointer;
}

.hamburger-icon span{
  width: 100%;
  height: 2px;
  background-color: black;
  transition: all 0.3 ease-in-out;
}

.menu-links{
  position: absolute;
  top: 100%;
  right: 0;
  background-color: white;
  width: fit-content;
  max-height: 0;
  overflow: hidden;
  transition: all 0.3 ease-in-out;
  z-index: 1;
}

.menu-links a{
  display: block;
  padding: 10px;
  text-align: center;
  font-size: 1.5em;
  color: black;
  text-decoration: none;
  transition: all 0.3 ease-in-out;
}

.menu-links li{
  list-style: none;
}

.menu-links.open{
  max-height: 300px;
}

.hamburger-icon.open span:first-child{
  transform: rotate(45deg) translate(10px, 5px);
}

.hamburger-icon.open span:nth-child(2){
  opacity: 0;
}

.hamburger-icon.open span:last-child{
  transform: rotate(-45deg) translate(10px, -5px);
}

.hamburger-icon span:first-child{
  transform: none;
}

.hamburger-icon span:first-child{
  opacity: 1;
}

.hamburger-icon span:first-child{
  transform: none;
}

/* BUTTONS */

a, .btn{
  transition: all 300ms ease;
}

.btn-container{
  display: flex;
  justify-content: center;
  gap: 1em;
}

.btn{
  font-weight: 600;
  transition: all 300ms ease;
  padding: 1rem;
  width: 8rem;
  border-radius: 2em;
}

.btn-color-1, .btn-color-2{
  border: rgb(38, 38, 38) 0.1em solid;
}

.btn-color-1:hover, .btn-color-2:hover{
  cursor: pointer;
}

.btn-color-1, .btn-color-2:hover{
  background: rgb(38, 38, 38);
  color: white;
}

.btn-color-1:hover{
  background: rgb(0, 0, 0);
}

.btn-color-2{
  background: none;
}

.btn-color-2:hover{
  border: rgb(255, 255, 255) 0.1em solid;
}

.btn-container{
  gap: 1em;
}

/* For project links */
.project-link{
  font-size: 0.84em;
  width: 6em;
}

.project-link:hover{
  text-decoration: none;
}

#sentence-link{
  color: rgb(0, 17, 255);
}

/* SECTIONS */

section{
  padding-top: 1vh;
  margin: 0 10em;
  box-sizing: border-box;
  min-height: fit-content;
}

.section-container{
  display: flex;
}

/* ICONS */

.icon{
  cursor: pointer;
  height: 2em;
}

/* PROFILE SECTION */

#profile {
  display: flex;
  justify-content: center;
  gap: 5em;
}

.profile-pic {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  object-fit: cover;
}

.section-pic-container{
  display: flex;
}

.section-text{
  align-self: center;
  text-align: center;
}

.section-text p{
  font-weight: 600;
}

.section-text-1{
  text-align: center;
}

.section-text-2{
  font-size: 1.75em;
  margin-bottom: 1em;
}

.title{
  font-size: 3em;
  text-align: center;
}

.section-header{
  margin-top: 2em;
}

#socials-container{
  display: flex;
  justify-content: center;
  margin-top: 1em;
  gap: 1em;
}

/* ABOUT SECTION */

#about{
  position: relative;
}

.about-containers{
  gap: 2em;
  margin-bottom: 2em;
  margin-top: 2em;
}

.about-details-container{
  justify-content: center;
  flex-direction: column;
}

.about-containers, .about-details-container{
  display: flex;
}

.about-pic{
  border-radius: 2em;
}


.details-container{
  padding: 1.5em;
  flex: 1;
  background: white;
  border-radius: 2em;
  border: rgb(53, 53, 53) 0.1em solid;
  border-color: rgb(163, 163, 163);
  text-align: center;
}

/* EXPERIENCE SECTION */

#experience {
  position: relative;
}

.experience-sub-title {
  color: rgb(85, 85, 85);
  font-weight: 600;
  font-size: 1.75em;
  margin-bottom: 2em;
}

.experience-details-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

#experience .article-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2em;
}

#experience article {
  align-self: start;
  justify-self: start;
  width: fit-content;
  margin-left: 1em;
}

.article-container{
  display: flex;
  text-align: initial;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 2.5em;
  justify-content: space-around;
}

article{
  display: flex;
  width: 10em;
  justify-content: space-around;
  gap: 0.5em;
}

article .icon{
  cursor: default;
}

/* PROJECTS SECTION */

#projects{
  position: relative;
}

.color-container{
  border-color: rgb(163, 163, 163);
  background: rgb(250, 250, 250);
}

.project-img{
  border-radius: 2em;
  width: 90%;
  height: 90%;
}

.project-title{
  margin: 1em;
  color: black;
}

.project-btn{
  color: black;
  border-color: rgb(163, 163, 163);
}

/* CONTACT */

#contact{
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 40vh;
}

.contact-info-upper-container{
  display: flex;
  justify-content: center;
  border-radius: 2em;
  border: rgb(53, 53, 53) 0.1em solid;
  border-color: rgb(163, 163, 163);
  background: (250, 250, 250);
  margin: 2em auto;
  padding: 0.5em;
}

.contact-info-container{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5em;
  margin: 1em;
}

.contact-info-container p{
  font-size: larger;
}

.contact-icon{
  cursor: default;
}

.email-icon{
  height: 2.5em;
}

/* FOOTER SECTION */

footer{
  height: 26vh;
  margin: 0 1em;
}

footer p{
  text-align: center;
}

/* PROJECT PURPOSE SECTION */

#project-purpose, #project-info{
  position: relative;
  /* height: 85vh; */
}

.project-containers{
  gap: 2em;
  margin-bottom: 2em;
  margin-top: 2em;
}

.project-details-container{
  justify-content: center;
  flex-direction: column;
}

.project-containers, .project-details-container{
  display: flex;
}

.project-pic{
  border-radius: 0.3em;
  width: 980px;
  height: 550px;
  margin-top: 1em;
}

.project-pic-medium{
  border-radius: 0.3em;
  width: 600px;
  height: 550px;
  margin-top: 1em;
}

.project-pic-side{
  border-radius: 0.3em;
  width: 200px;
  height: 550px;
  margin-top: 1em;
  margin-left: 1em;
}

.project-pic-container{
  display: flex;
}

#kangaroo-taskaroo-search-task, #weather-search-bar{
  width: 815px;
  margin-top: 1em;
}

#kangaroo-taskaroo-search-task{
  border-radius: 0.3em;
}

#data-viewer-map-table{
  width: 500px;
  height: 400px;
  border-radius: 5px;
}

.bullet-point-list{
  margin-left: 2.5em;
}